<template>
    <div class="paystate">
        <!-- header -->
        <headertop></headertop>
        <div class="container">
            <!-- 0: -1:/ 2: -->
            <div v-if="paystate != 2" class="state-main cancle">
                <!-- / -->
                <Icon type="close-circled"></Icon>
                <p></p>
                <span></span>
                <Button type="info" @click="backIndex"></Button>
            </div>
            <div v-if="paystate == 2" class="state-main success">
                <!--  -->
                <Icon type="checkmark-circled"></Icon>
                <p></p>
                <Button type="success"  @click="backList"></Button>
            </div>
        </div>
        <!-- footer -->
        <footersimple></footersimple>

    </div>
</template>

<script>
/* header */
import headertop from "@/components/headertop";
/* footer */
import footersimple from "@/components/footerSimple";

export default {
  data() {
    return {
      paystate: 2
    };
  },
  components: {
    headertop,
    footersimple
  },
  methods: {
    //  
    getStateData() {
      // 
      this.paystate = this.$route.query.orderType;
    },
    // 
    backIndex(){
      // 
      this.$router.push({ path: '/' })
    },
    // 
    backList(){
      this.$router.push({ path: '/orderlist' })      
    }
  },
  mounted() {
    // 
    this.getStateData();
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../static/less/custom.less";

.container {
  margin-top: 10px;
  margin-bottom: 10px;
  background-color: @body-background;
  padding: 80px 0;
  .state-main {
    margin: 0 auto;
    text-align: center;
    font-size: 50px;
    color: @primary-color;
    span {
      line-height: 25px;
      color: #666;
      font-size: 14px;
      display: block;
    }
  }
  .success {
    color: #09bb07;
  }
  p {
    line-height: 50px;
    font-size: 30px;
    color: #333;
  }
}
</style>



