<template>
  <div class="personinfo">
    <Tabs type="card" :animated="false" name="info1" @on-click="">
      <Tab-pane name="info1" label="">
        <p class="tip"></p>
        <!--  -->
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
          <FormItem label="" prop="nickName">
            <Input v-model="formValidate.nickName" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="phone">
            <Input v-model="formValidate.phone" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="realName">
            <Input v-model="formValidate.realName" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="idCard">
            <Input v-model="formValidate.idCard" placeholder=""></Input>
          </FormItem>
          <FormItem label="" prop="birthday">
            <DatePicker format="yyyy-MM-dd" type="date" placeholder="" v-model="formValidate.birthday"></DatePicker>
          </FormItem>
          <FormItem label="" prop="sex">
            <RadioGroup v-model="formValidate.sex">
              <Radio label="0"></Radio>
              <Radio label="1"></Radio>
            </RadioGroup>
          </FormItem>
          <FormItem>
            <Button type="primary" @click="handleSubmit('formValidate')"></Button>
          </FormItem>
        </Form>
        <!--  -->
      </Tab-pane>
      <Tab-pane name="info2" label="">
        <!--  -->
        <div class="clearfix">
          <div class="upload-main fl">
            <Upload ref="upload" :show-upload-list="false" :on-success="handleSuccess" :format="['jpg','jpeg','png']" :max-size="2048" :on-format-error="handleFormatError" :on-exceeded-size="handleMaxSize" multiple type="drag" action="//jsonplaceholder.typicode.com/posts/" style="display: inline-block;">
              <div style="width: 158px;height:158px;line-height: 188px;">
                <Icon type="camera" size="60"></Icon>
              </div>
            </Upload>
            <p>,jpgjpegpng</p>
          </div>
          <div class="demo-upload-list fl">
            <!--  -->
            <img v-if="flag" :src="formValidate.headUrl">
            <!-- icon -->
            <Icon v-if="uploadList.status != 'finished'" type="person" style="font-size:50px"></Icon>
            <template v-if="uploadList.status === 'finished'">
              <img :src="uploadList.url">
            </template>
          </div>
        </div>

        <!--  -->
      </Tab-pane>
      <Tab-pane name="info3" label="">
        <div style="width:500px">
          <Input v-model="formValidate.interest" type="textarea" :rows="4" placeholder="..."></Input>
          <Button style="margin-top:30px;" type="primary" @click="submitinterest"></Button>
        </div>
      </Tab-pane>

    </Tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      formValidate: {
        // 
        nickName: "",
        // 
        phone: "",
        // 
        realName: "",
        // 
        idCard: "",
        // 
        sex: "",
        // 
        birthday: "",
        // 
        headUrl: "",
        // 
        interest: ""
      },
      // 
      ruleValidate: {
        nickName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        phone: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      //  
      defaultList: [
        {
          name: "bc7521e033abdd1e92222d733590f104",
          url:
            "https://o5wwk8baw.qnssl.com/bc7521e033abdd1e92222d733590f104/avatar"
        }
      ],
      imgName: "",
      uploadList: {},
      // 
      flag: false,
      isValidate: ""
    };
  },
  components: {},
  props: ["infos", "isinfo"],
  methods: {
    // 0-1
    isContains(str, substr) {
      return str.indexOf(substr) >= 0;
    },
    // 
    handleSubmit() {
      // console.log("=" + JSON.stringify(this.formValidate));
      let nickName = this.isContains(
        this.isValidate,
        this.formValidate.nickName
      );
      let phone = this.isContains(this.isValidate, this.formValidate.phone);
      let realName = this.isContains(
        this.isValidate,
        this.formValidate.realName
      );
      let idCard = this.isContains(this.isValidate, this.formValidate.idCard);
      let sex = this.isContains(this.isValidate, this.formValidate.sex);
      //   
      let str = JSON.stringify(this.formValidate.birthday);
      let strsub = str.substring(0, 11);
      let birthday = this.isContains(this.isValidate, strsub);
      // 
      let formFlag = nickName && phone && realName && idCard && sex && birthday;
      console.log("formFlag = " + formFlag);
      if (formFlag) {
        this.$Notice.warning({
          title: ""
        });
      } else {
        this.$http
          .postRequest({
            type: "modifyPersonInfo",
            data: this.formValidate
          })
          .then(res => {
            //  
            console.log(" = " + res.data);
          });
      }
    },
    // 
    // 
    handleSuccess(res, file, fileList) {
      // tokentoken
      console.log("file=" + JSON.stringify(file));
      console.log("fileList=" + JSON.stringify(fileList[0]));
      //file={"status":"finished","name":"1.png","size":7525,"percentage":100,"uid":1515392597210,"showProgress":true,"response":{"id":101}}
      /* file.url =
        "https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar";
      file.name = "7eb99afb9d5f317c912f08b5212fd69a"; */

      // console.log("uploadList"+ JSON.stringify(this.uploadList))
      this.uploadList = fileList[0];
      this.uploadList.name = "7eb99afb9d5f317c912f08b5212fd69a";
      this.uploadList.url =
        "https://o5wwk8baw.qnssl.com/7eb99afb9d5f317c912f08b5212fd69a/avatar";
    },
    // 
    handleFormatError(file) {
      this.$Notice.warning({
        title: "",
        desc: " " + file.name + "  jpg  png "
      });
    },
    // 
    handleMaxSize(file) {
      this.$Notice.warning({
        title: "",
        desc: " " + file.name + "  2M"
      });
    },
    // 
    /*  handleBeforeUpload() {
      const check = this.uploadList.length < 5;
      if (!check) {
        this.$Notice.warning({
          title: "."
        });
      }
      return check; 
    }, */
    // 

    // 
    submitinterest() {
      // 
      if (this.isContains(this.isValidate, this.formValidate.interest)) {
        this.$Notice.warning({
          title: ""
        });
      } else {
        this.$http
          .postRequest({
            type: "modifyPersonInfo",
            data: {
              interest: this.formValidate.interest
            }
          })
          .then(res => {
            //  
            console.log("123 = " + res.data);
          });
      }
    }
    // 
  },
  // 
  watch: {
    infos: function() {
      this.formValidate = this.infos;
      /* console.log(
        "formValidate--this.infos = " + JSON.stringify(this.formValidate)
      ); */
      // 
      this.flag =
        this.formValidate.headUrl != "" && this.uploadList.status != "finished";
    },
    isinfo: function() {
      this.isValidate = this.isinfo;
    },
    uploadList: function() {
      // 
      this.flag =
        this.formValidate.headUrl != "" && this.uploadList.status != "finished";
    }
  },
  mounted() {
    // 
    this.uploadList = this.$refs.upload.fileList;
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../../static/less/custom.less";

.personinfo {
  .tip {
    background: #fffdee;
    border: 1px solid #edd28b;
    padding: 10px 20px;
    color: #999;
    margin-bottom: 15px;
  }

  .upload-main {
    height: 400px;
    width: 400px;
    text-align: center;
    p {
      line-height: 30px;
    }
  }
}
</style>
<style lang="less">
.personinfo {
  .ivu-tabs {
    // border: 1px solid #ececec;
    padding-bottom: 10px;
  }
  .ivu-tabs-bar {
    margin-bottom: 0;
  }
  .ivu-tabs-content {
    border: 1px solid #ececec;
    border-top: none;
    padding: 20px 20px 120px;
  }
  .ivu-form {
    width: 400px;
  }
  // 
  .demo-upload-list {
    // display: inline-block;
    width: 150px;
    height: 150px;
    text-align: center;
    line-height: 150px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
    margin-right: 4px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .demo-upload-list-cover {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    i {
      color: #fff;
      font-size: 20px;
      cursor: pointer;
      margin: 0 2px;
    }
  }
  .demo-upload-list:hover {
    .demo-upload-list-cover {
      display: block;
    }
  }
  // 
}
</style>




