<template>
    <div class="recentorder">
        <!--  -->
        <div class="orderlist">
            <div class="hd">
                <h2 class="hd-title"></h2>
            </div>
            <div class="filter-order clearfix">
                <div class="fl">
                    <Select v-model="ordertypeval" style="width:150px" @on-change="handelOrderType">
                        <Option v-for="item in orderTypeSelect" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                    <Select v-model="creattimeval" style="width:150px" @on-change="handelOrderTime">
                        <Option v-for="item in creatTime" :value="item.value" :key="item.value">{{ item.label }}</Option>
                    </Select>
                </div>
                <div class="fr">
                    <Input v-model="keywordval" placeholder="..." style="width: 300px"></Input>
                    <Button type="ghost" @click="addKeyWord"></Button>
                </div>

            </div>
            <div class="orderlist-main">
                <Table border :columns="columns1" :data="data1"></Table>
            </div>
        </div>
        <!--  -->
    </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      columns1: [
        {
          title: "",
          key: "orderNo"
        },
        {
          title: "",
          key: "sellTime"
        },
        {
          title: "",
          key: "itemName"
        },
        {
          title: "",
          key: "num",
          width: 80
        },
        {
          title: "",
          key: "unitPrice",
          width: 80
        },
        {
          title: "",
          key: "totalAmount",
          width: 100
        },
        {
          title: "",
          key: "orderType",
          width: 100
        },
        {
          title: "",
          key: "operate",
          width: 80
          /* align: 'center',
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "success",
                    size: "small"
                  },
                  on: {
                    click: () => {
                    //   this.show(params.index);
                    }
                  }
                },
                ""
              )
            ]);
          } */
        }
      ],
      data1: [],
      // (0: -1:/ 2:  )
      orderTypeSelect: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      ordertypeval: "",
      // 
      creatTime: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "3",
          label: "3"
        }
      ],
      creattimeval: "",
      // 
      keywordval: "",
      // 
      orderType: "",
      // 
      orderTime: 0,
      //  
      keyword: ""
    };
  },
  components: {},
  methods: {
    // 
    getOrderData() {
      this.$http
        .postRequest({
          type: "queryOrderList",
          data: {
            // 
            orderType: this.orderType,
            orderTime: this.orderTime,
            keyword: this.keyword
          }
        })
        .then(res => {
          // 
          // console.log(" = " + JSON.stringify(res.data));
          //  data1
          for (var i in res.data) {
            // res.data[i]
            if (res.data[i].orderType == 0) {
              res.data[i].orderType = "";
            } else if (res.data[i].orderType == -1) {
              res.data[i].orderType = "";
            } else {
              res.data[i].orderType = "";
            }
          }
          this.data1 = res.data;
        });
    },
    // 
    // (0: -1:/ 2:)
    handelOrderType() {
        // console.log(this.ordertypeval);
        // 
      if (this.ordertypeval == "") {
        this.orderType = "";
      } else if (this.ordertypeval == "") {
        this.orderType = 0;
      } else if (this.ordertypeval == "") {
        this.orderType = -1;
      } else {
        this.orderType = 2;
      }
      // console.log(this.orderType);
      // 
      this.getOrderData();
    },
    // 
    // (0:,1:,2:)
    handelOrderTime() {
      // console.log(this.creattimeval);
      if (this.creattimeval == "") {
        this.orderTime = 0;
      } else if (this.creattimeval == "") {
        this.orderTime = 1;
      } else {
        this.orderTime = 2;
      }
      // 
      this.getOrderData();
    },
    // 
    // 
    addKeyWord() {
      this.keyword = this.keywordval;
      //   console.log("="+this.keyword)

      // 
      this.getOrderData();
      //  
      this.keywordval = "";
    }
  },
  mounted() {
    //  
    this.getOrderData();
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../../static/less/custom.less";
.hd {
  position: relative;
  border-bottom: 1px solid #d7d7d7;
  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 0;
    width: 120px;
    border-bottom: 1px solid @primary-color;
  }
  .hd-title {
    color: @primary-color;
    line-height: 30px;
    font-size: 16px;
    font-weight: 400;
    padding-left: 10px;
  }
}
//
.orderlist {
  margin-top: 20px;
  .orderlist-main {
    padding: 10px 0;
  }
  .filter-order {
    margin: 15px 0;
  }
}
</style>



