<template>
  <div class="paymethod">
    <!-- header -->
    <headertop></headertop>
    <div class="container">
      <!-- step -->
      <!-- <div class="steps">
        <Steps :current="2">
          <Step title=""></Step>
          <Step title=""></Step>
          <Step title=""></Step>
          <Step title=""></Step>
        </Steps>
      </div> -->

      <div class="choose-seat-container">
        <div class="choose-seat-head">
          <div class="head-first">1 </div>
          <div class="head-third">2 </div>
          <div class="head-second">3 </div>
          <div class="head-third">4 </div>
        </div>
      </div>
      <!-- step -->

      <!--  -->
      <div class="orderlist">
        <div class="orderlist-main">
          <Table border :columns="columns1" :data="data1"></Table>
        </div>
        <p>:
          <span>{{this.data1[0].payAmount}}</span> </p>
      </div>
      <!--  -->

      <!--  -->
      <div class="payment">
        <p>:</p>
        <!--  -->
        <div class="pay-main">
          <RadioGroup v-model="payway" @on-change="handleRadio">
            <Radio label=""><img :src="imgUrlWx"></Radio>
            <Radio label=""><img :src="imgUrlZfb"></Radio>
          </RadioGroup>
        </div>
        <Button type="primary" @click="submitPay"></Button>
      </div>
      <!--  -->
    </div>
    <!-- footer -->
    <footersimple></footersimple>
  </div>
</template>

<script>
/* header */
import headertop from "@/components/headertop";
/* footer */
import footersimple from "@/components/footerSimple";

import img_wx from "./img/wx_10127.jpg";
import img_zfb from "./img/zfb_8129.jpg";

export default {
  data() {
    return {
      // 
      columns1: [
        {
          title: "",
          key: "orderNo"
        },
        {
          title: "",
          key: "itemName"
        },
        {
          title: "",
          key: "totalAmount"
        },
        {
          title: "",
          key: "payAmount"
        }
      ],
      data1: [
        {
          orderNo: "",
          itemName: "",
          totalAmount: 0,
          payAmount: 0
        }
      ],
      payway: "",
      imgUrlWx: img_wx,
      imgUrlZfb: img_zfb,
      // 
      orderNo: "1234",
      paytype: "",
      zfbUrl: "https://www.baidu.com",
      // 
      wxdata: [],
      // 
      ordertype: 0
    };
  },
  components: {
    headertop,
    footersimple
  },
  mounted() {
    // 
    this.orderInfos();
    // 
    this.orderNo = this.$route.query.orderNo;
  },
  methods: {
    //   this.orderNo

    // 
    orderInfos() {
      this.$http
        .postRequest({
          type: "confirmPay",
          data: {
            // 
            orderNo: this.orderNo
          }
        })
        .then(res => {
          // 
          //     payAmount
          res.data.payAmount = res.data.totalAmount;
          // 
          //   this.data1[0] = res.data;
          //   this.data1[0].payAmount =res.data.totalAmount;
          //   console.log(" = " + JSON.stringify([res.data]));
          this.data1 = [res.data];
        });
    },
    handleRadio(e) {
      this.paytype = e;
      if (this.paytype == "") {
        this.zfbUrl = "https://www.baidu.com";
      } else {
        this.$http
          .postRequest({
            type: "weChatPay",
            data: {
              // 
              orderNo: this.orderNo
            }
          })
          .then(res => {
            // 
            // console.log ("=" + JSON.stringify( res.data ));
            this.wxdata = res.data;
          });
      }
    },
    // 
    getOrderStatus() {
      let timer = null;
      // 
      let timeout = 2 * 60 * 60;
      clearTimeout(timer);
      this.$http
        .postRequest({
          type: "orderState",
          data: {
            // 
            orderNo: this.orderNo
          }
        })
        .then(res => {
          // 
          //  console.log ("=" + JSON.stringify( res.data ));
          if (res.data.orderType === 2) {
            this.ordertype = res.data.orderType;
            this.$router.push({
              path: "orderstate",
              query: { orderType: this.ordertype }
            });
          } else {
            timer = setTimeout(this.getOrderStatus, 1000);
            timeout--;
          }
          // ,
          if (timeout < 0) {
            this.$router.push({
              path: "orderstate",
              query: { orderType: 0 }
            });
          } else if (res.data.orderType === -1) {
            this.$router.push({
              path: "orderstate",
              query: { orderType: res.data.orderType }
            });
          }
        });
    },
    // 
    submitPay() {
      if (this.paytype == "") {
        /* Vuewww.baidu.comhttps:// */
        // 
        window.open(this.zfbUrl);
        // 
        // window.location.href = this.zfbUrl ;
      } else {
        // 
        this.$router.push({ path: "wechatpay", query: this.wxdata });
        // 
        this.getOrderStatus();
      }
    }
    //
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../static/less/custom.less";

@top: 50px;
@red: #ff3c1b;
.paymethod {
  .container {
    padding: 0 20px 50px;
    background-color: @body-background;
  }
  .steps {
    padding: 30px 0;
  }
  //
  .orderlist {
    margin-top: 20px;
    .orderlist-main {
      padding: 10px 0;
    }
    p {
      font-size: 16px;
    }
    span {
      color: @primary-color;
      margin: 0 5px;
    }
  }
  //  
  .payment {
    margin-top: 10px;
    .pay-main {
      margin: 15px 0;
      line-height: 30px;
      img {
        vertical-align: middle;
      }
    }
  }

    // step
  .choose-seat-container {
    height: 50px;
    position: relative;
    .choose-seat-head {
      width: 1160px;
      position: absolute;
      top: 10px;
      z-index: 1;
      div {
        float: left;
        display: inline;
        width: 25%;
        height: 50px;
        background: #65a9fd;
        color: #fff;
        text-align: center;
        line-height: 50px;
        font-size: 18px;
        background: linear-gradient(
          #565656,
          #484848,
          #3e3e3e,
          #484848,
          #565656
        );
      }

      .head-second {
        background: #ff3c1b;
        position: relative;
        height: 52px;
        top: -2px;
      }
      .head-second:before {
        content: "";
        position: absolute;
        display: block;
        top: 0px;
        width: 60px;
        border: 25px solid @red;
        border-bottom: 26px solid @red;
        border-top: 26px solid @red;
        height: 50px;
        z-index: 1;
      }
      .head-second:after {
        content: "";
        position: absolute;
        display: block;
        top: -1px;
        width: 60px;
        border: 27px solid @red;
        border-left: 30px solid @red;
        height: 54px;
        z-index: 1;
      }
      .head-second:before {
        left: -26px;
        border-left-color: transparent;
      }
      .head-second:after {
        right: -60px;
        border-top-color: transparent;
        border-bottom-color: transparent;
        border-right-color: transparent;
      }
    }
  }
  // step  

}
</style>



