<template>
  <div class="wechat">
    <!-- header -->
    <headertop></headertop>
    <div class="container clearfix">
      <div class="code-main fl">
        <p>2</p>
        <canvas id="canvas"></canvas>
        <p></p>
      </div>
      <div id='code' class="fl">
        <p></p>
        <p> :
          <span>{{this.orderinfos.orderNo}}</span>
        </p>
        <p> :
          <span>{{this.orderinfos.itemName}}</span>
        </p>
        <p> :
          <span>
            <i>{{this.orderinfos.totalAmount}}</i>
          </span>
        </p>
      </div>
    </div>
    <!-- footer -->
    <footersimple></footersimple>

  </div>
</template>
<script>
/* header */
import headertop from "@/components/headertop";
/* footer */
import footersimple from "@/components/footerSimple";

import Vue from "vue";
import QRCode from "qrcode";
Vue.use(QRCode);
export default {
  data() {
    return {
      codes: "",
      orderinfos: ""
    };
  },
  components: {
    headertop,
    footersimple
  },
  methods: {
    // 
    useqrcode() {
      // 
      var canvas = document.getElementById("canvas");
      //  ---
      QRCode.toCanvas(canvas, this.orderinfos.codeUrl, function(error) {
        if (error) {
          console.error(error);
        } else {
          // 
          // console.log("success!");
        }
      });
    },
    // 
    getQueryData() {
      // 
      this.orderinfos = this.$route.query;
      // console.log(" = " + JSON.stringify(this.orderinfos));
    }
  },
  mounted() {
    //
    this.getQueryData();
    // 
    this.useqrcode();
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../static/less/custom.less";

.container {
  margin-top: 10px ;
  margin-bottom: 10px ;
  padding: 80px 0;
  background-color: @body-background;
  #code {
    p {
      line-height: 40px;
      color: #333;
      font-size: 16px;
      span {
        margin-left: 10px;
        i {
          color: @primary-color;
          font-weight: bold;
        }
      }
    }
  }
}
.code-main {
  margin: 0 150px;
  p {
    text-align: center;
    line-height: 30px;
    font-size: 14px;
  }
  #canvas {
    width: 200px !important;
    height: 200px !important;
  }
}
</style>



