<template>
  <div>
    <!--  -->
    <Vheader/>
    <div class="container">
      <div class="list-top">
        <p id="navigation_show">
          <span class="list-cf">{{this.itemdescp.total}}</span> </p>
      </div>
      <!--  -->
      <div class="list-con clearfix">
        <div class="list-left fl">
          <!--  -->
          <div class="list-condition clearfix">
            <!--  -->
            <dl class="clearfix">
              <dt> :</dt>
              <dd>
                <a href="javascript:;" :class="{active:flag}" @click="queryAll('city')"></a>
              </dd>
              <dd class="list-city-item">
                <ul>
                  <li v-for="(item,index) in city" :key="index" @click="queryID(item.id,index,'city')">
                    <a href="javascript:;" :class="{'active':ind === index}">{{item.name}}</a>
                  </li>

                </ul>
              </dd>
            </dl>
            <!--  -->
            <!--  -->
            <dl class="clearfix">
              <dt> :</dt>
              <dd>
                <a href="javascript:;" :class="{active:categrayflag}" @click="queryAll('categray')"></a>
              </dd>
              <dd class="list-city-item">
                <ul>
                  <li v-for="(item,index) in categray" :key="index" @click="queryID(item.id,index,'categray')">
                    <a href="javascript:;" :class="{'active':categrayind === index}">{{item.itemType}}</a>
                  </li>
                </ul>
              </dd>
            </dl>
            <!--  -->
            <!--  -->
            <dl class="clearfix" v-show="!categrayflag">
              <dt> :</dt>
              <dd>
                <a href="javascript:;" :class="{active:soncategrayflag}" @click="queryAll('soncategray')"></a>
              </dd>
              <dd class="list-city-item">
                <ul>
                  <li v-for="(item,index) in soncategray" :key="index" @click="queryID(item.id,index,'soncategray')">
                    <a href="javascript:;" :class="{'active':soncategrayind === index}">{{item.itemType}}</a>
                  </li>
                </ul>
              </dd>
            </dl>
            <!--  -->
            <!--  -->
            <dl class="child clearfix">
              <dt> :</dt>
              <dd>
                <a href="javascript:;" :class="{active:timeflag}" @click="queryAll('time')"></a>
              </dd>
              <dd class="list-city-item">
                <ul>
                  <li @click="querytime('today')">
                    <a href="javascript:;" :class="{'active':timeind == 1}"></a>
                  </li>
                  <li @click="querytime('tomorrow')">
                    <a href="javascript:;" :class="{'active':timeind == 2}"></a>
                  </li>
                  <li>
                    <Date-picker type="date" size="small" placeholder="" style="width: 150px" @on-change="handlestart"></Date-picker>
                  </li>
                  <li>-</li>
                  <li>
                    <Date-picker type="date" size="small" placeholder="" style="width: 150px" @on-change="handleChange"></Date-picker>
                  </li>
                </ul>
              </dd>
            </dl>
            <!--  -->
          </div>
          <!--  -->

          <!--  -->
          <div class="list-main">
            <Tabs type="card" :animated="false" name="name2" @on-click="recommend()">
              <Tab-pane name="name1" label="">
                <!--  -->
                <ul class="clearfix">
                  <listitem :switchlist="(buttonswitch=='list')?'list':'big'" v-for="(item, index) in itemdata" :key="index" :item-info="item"></listitem>
                </ul>
                <!-- start -->
                <!-- <ul v-show="buttonswitch=='big'" class="list-item clearfix">
                  <listitembig v-for="item in itemdata" :item-info="item"></listitembig>
                </ul> -->

                <!--  -->
                <div v-show="this.itemdescp.total > 16" class="pagena">
                  <Page :current="1" :total="this.itemdescp.total" @on-change="pageclick" :page-size="pagesize" show-total></Page>
                </div>
              </Tab-pane>
              <Tab-pane name="name2" label="">
                <!--  -->
                <ul class="clearfix">
                  <listitem :switchlist="(buttonswitch=='list')?'list':'big'" v-for="(item, index) in itemdata" :key="index" :item-info="item"></listitem>
                </ul>
                <!--  -->
                <div v-show="this.itemdescp.total > 16" class="pagena">
                  <Page :current="1" :total="this.itemdescp.total" @on-change="pageclick" :page-size="pagesize" show-total></Page>
                </div>

              </Tab-pane>
              <Tab-pane name="name3" label="">
                <!--  -->
                <ul class="clearfix">
                  <listitem :switchlist="(buttonswitch=='list')?'list':'big'" v-for="(item, index) in itemdata" :key="index" :item-info="item"></listitem>
                </ul>

                <!--  -->
                <div v-show="this.itemdescp.total > 16" class="pagena">
                  <Page :current="1" :total="this.itemdescp.total" @on-change="pageclick" :page-size="pagesize" show-total></Page>
                </div>

              </Tab-pane>

            </Tabs>
            <div class="list-radio">
              <RadioGroup v-model="buttonswitch" type="button">
                <Radio label="list">
                  <Icon type="navicon-round"></Icon>
                </Radio>
                <Radio label="big">
                  <Icon type="social-windows"></Icon>
                </Radio>
              </RadioGroup>
            </div>

          </div>
          <!--  -->
        </div>
        <!--   -->
        <div class="list-right fr">
          <div class="list-box">
            <p class="list-box-title"></p>
            <div class="list-box-con">
              <!--  -->
              <listitemlike v-for="(item, index) in guessinfo" :key="index" :guess="item"></listitemlike>
            </div>
          </div>
        </div>
        <!--   -->

      </div>

    </div>

    <!--  -->
    <Vfooter/>

  </div>
</template>

<script>
import header from "@/components/header";
import footer from "@/components/footer";
// 
import listitem from "@/components/ticketlist/listitem";
// 
import listitemlike from "@/components/ticketlist/listitemlike";

// import { history } from 'vue-router'

export default {
  data() {
    return {
      city: [],
      ind: "",
      categrayind: "",
      soncategrayind: "",
      timeind: "",
      // 
      flag: true,
      categrayflag: true,
      soncategrayflag: true,
      timeflag: true,
      // 
      areaId: 0,
      // 
      parent: 0,
      itemTypeId1: 0,
      categray: [],
      // 
      itemTypeId2: 0,
      soncategray: "",
      // 
      startTime: "",
      endTime: "",
      // 
      sort: "",
      // 
      itemdescp: "",
      itemdata: [],
      // button
      buttonswitch: "list",
      // 
      itemTypeId: "0",
      guessinfo: [],
      // 
      currentpage: 1,
      pagesize: 16,
      // 
      keyword: ""
    };
  },
  components: {
    Vheader: header,
    Vfooter: footer,
    listitem,
    listitemlike
  },
  // 
  mounted() {
    // 
    // console.log("keyword="+ this.$route.query.keyword)
    if (this.$route.query.keyword != "") {
      this.keyword = this.$route.query.keyword;
    }
    // 
    if (
      this.$route.query.itemTypeId1 != "" &&
      this.$route.query.itemTypeId2 == ""
    ) {
      // 
      /* 
      
      1.
      2.ID
      3id
      */
      // 
      this.itemTypeId1 = this.$route.query.itemTypeId1;

      this.listPost();
      this.itemdescp.forEach((ele, index) => {
        if (ele.id == this.$route.query.itemTypeId1) {
          //   ele.itemType;
          /* liactive */
          this.categrayind = index;
          /* li active */
          this.categrayflag = false;
          // id  parent
          this.parent = ele.id;
          //  2. id
          this.$http
            .postRequest({
              type: "listSortGoods",
              data: { parent: this.parent }
            })
            .then(res => {
              // console.log(" = " + JSON.stringify(res.data));
              this.categray = res.data;
            });
        }
      });
    } else if (
      this.$route.query.itemTypeId1 != "" &&
      this.$route.query.itemTypeId2 != ""
    ) {
      // 
      /* 
      
      1.
      2.ID
      3id
      4.id
      */
      // 
      /* this.itemTypeId1 = this.$route.query.itemTypeId1;
      this.itemTypeId2 = this.$route.query.itemTypeId2;

      this.listPost();
      this.itemdescp.forEach((ele, index) => {
        // 
        if (ele.id == this.$route.query.itemTypeId1) {
          //   ele.itemType;

          // liactive
          this.categrayind = index;
          // li active
          this.categrayflag = false;
          // id  parent
          this.parent = ele.id;
          //  2. id
          this.$http
            .postRequest({
              type: "listSortGoods",
              data: { parent: this.parent }
            })
            .then(res => {
              // console.log(" = " + JSON.stringify(res.data));
              this.categray = res.data;
            });
        }
        // 
        if (ele.id == this.$route.query.itemTypeId2) {
          // liactive
          this.soncategrayind = index;
          // li active
          this.soncategrayflag = false;
        }
      }); */
    }

    //  1. API()
    this.$http.postRequest({ type: "listQueryCity" }).then(res => {
      // console.log(res.data);
      this.city = res.data;
    });
    //  2. 
    this.$http
      .postRequest({ type: "listSortGoods", data: { parent: this.parent } })
      .then(res => {
        // console.log(" = " + JSON.stringify(res.data));
        this.categray = res.data;
      });
    //3. 
    // 
    this.listPost();

    //4 
    this.guess();
  },
  methods: {
    //  0
    queryAll(name) {
      if (name == "city") {
        this.areaId = 0;
        this.flag = true;
        this.ind = "";
        // 
        this.listPost();
      } else if (name == "categray") {
        this.itemTypeId1 = 0;
        this.categrayflag = true;
        this.categrayind = "";
        // 
        this.listPost();

        // 
        this.itemTypeId = "0";
        this.guess();
      } else if (name == "soncategray") {
        this.itemTypeId2 = 0;
        this.soncategrayflag = true;
        this.soncategrayind = "";
        // 
        this.listPost();
        // 
        this.itemTypeId = "0";
        this.guess();
      } else {
        this.startTime = "";
        this.endTime = "";
        this.timeflag = true;
        this.timeind = "";
        // 
        this.listPost();
      }
    },
    // ID
    queryID(id, index, name) {
      if (name == "city") {
        this.areaId = id;
        /* liactive */
        this.ind = index;
        /* li active */
        this.flag = false;
        // 
        this.listPost();
      } else if (name == "categray") {
        // alert(id);
        this.itemTypeId1 = id;
        this.itemTypeId = id;
        /* liactive */
        this.categrayind = index;
        /* li active */
        this.categrayflag = false;

        // 
        this.listPost();

        //
        this.parent = id;

        //  
        this.listcategray();

        // 
        this.guess();
      } else if (name == "soncategray") {
        this.itemTypeId2 = id;
        this.itemTypeId = id;
        /* liactive */
        this.soncategrayind = index;
        /* li active */
        this.soncategrayflag = false;
        // 
        this.listPost();

        // 
        this.guess();
      }
    },
    // 
    GetDateStr(AddDayCount) {
      var dd = new Date();
      dd.setDate(dd.getDate() + AddDayCount); //AddDayCount
      var y = dd.getFullYear();
      var m = dd.getMonth() + 1; //
      var d = dd.getDate();
      // 
      if (m < 10) {
        m = "0" + m;
      } else {
        m = m;
      }
      // n
      if (d < 10) {
        //<10
        d = "0" + d;
      } else {
        d = d;
      }
      return y + "-" + m + "-" + d;
    },
    // 
    querytime(date) {
      if (date == "today") {
        //  console.log( this.GetDateStr(1) );
        this.timeind = 1;
        this.timeflag = false;
        this.startTime = this.GetDateStr(0);
        this.endTime = this.GetDateStr(0);
        // 
        this.listPost();

        this.startTime = "";
        this.endTime = "";
      } else if (date == "tomorrow") {
        this.timeind = 2;
        this.timeflag = false;
        this.startTime = this.GetDateStr(1);
        this.endTime = this.GetDateStr(1);

        // 
        this.listPost();

        this.startTime = "";
        this.endTime = "";
      }
    },
    // startTime
    handlestart(date) {
      this.startTime = date;
    },
    handleChange(date) {
      this.endTime = date;
      if (this.startTime != "") {
        // 
        this.listPost();

        this.startTime = "";
        this.endTime = "";
      } else {
        this.$Message.info("");
      }
    },
    // 
    listcategray() {
      this.$http
        .postRequest({ type: "listSortGoods", data: { parent: this.parent } })
        .then(res => {
          // console.log(" = " + JSON.stringify(res.data));
          if (res.data.length > 0) {
            this.soncategray = res.data;
          } else {
            this.categray = true;
          }
        });
    },
    // 
    listPost() {
      //3. 
      this.$http
        .postRequest({
          type: "listQueryGoodsInfos",
          data: {
            areaId: this.areaId,
            itemTypeId1: this.itemTypeId1,
            itemTypeId2: this.itemTypeId2,
            startTime: this.startTime,
            endTime: this.endTime,
            // 
            sort: this.sort,
            // 
            currentPage: this.currentpage,
            pageSize: this.pagesize,
            keyword: this.keyword
          }
        })
        .then(res => {
          // 
          this.itemdescp = res.data;
          this.itemdata = res.data.rows;
          // console.log(" = " + JSON.stringify(this.itemdata));
        });
    },
    recommend(e) {
      // console.log(e);
      let type = e;
      if (type == "name1") {
        this.sort = "recommend";
        // this.itemTypeId1 = 0;
        // 
        this.listPost();
      } else if (type == "name2") {
        this.sort = "recentShow";
        // this.itemTypeId1 = 1;
        // 
        this.listPost();
      } else {
        this.sort = "recentSell";
        // 
        this.listPost();
      }
    },
    guess() {
      //4. 
      this.$http
        .postRequest({
          type: "listGuessLike",
          data: {
            itemTypeId: this.itemTypeId
          }
        })
        .then(res => {
          // 
          this.guessinfo = res.data;
          // console.log(" = " + JSON.stringify(this.guessinfo));
        });
    },
    // 
    pageclick(e) {
      // console.log(e);
      // 
      this.currentpage = e;
      // 
      this.listPost();
    }
    // id
    /* gainguessid(id) {
      // console.log("id="+id)
      // console.log("query="+query)
      // url
      this.$router.push({
        path: "/ticketDesc",
        name: "TicketDesc",
        params: {
          name: "TicketDesc",
          id: id
        }
      });
    } */
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../static/less/custom.less";

.list-top {
  line-height: 50px;
  color: #666;
  font-size: 14px;
  background-color: @body-background;
  p {
    padding-left: 20px;
  }
  .list-cf {
    color: @primary-color;
  }
}

.list-left {
  width: 928px;
  .list-condition {
    padding: 10px 24px 0;
    border: 1px solid #e9e9e9;
    margin-bottom: 15px;
    font-size: 14px;
    background-color: @body-background;
    dl {
      border-bottom: 1px dotted #dfdfdf;
      margin-bottom: 10px;
      padding-bottom: 5px;
      dt {
        width: 80px;
        float: left;
        line-height: 26px;
        color: #3b3a3a;
        text-align: right;
        margin-right: 18px;
      }
      dd {
        width: 50px;
        float: left;
        a {
          display: inline-block;
          height: 26px;
          line-height: 26px;
          padding: 0 8px;
          color: #333;
          white-space: nowrap;
        }
        .active {
          background-color: @primary-color;
          color: #fff;
        }
      }
      .list-city-item {
        width: 730px;
        ul {
          height: 62px;

          li {
            padding-bottom: 5px;
            float: left;
            margin-right: 24px;
            a {
              &:hover {
                background-color: @primary-color;
                color: #fff;
              }
            }
          }
        }
      }
      &:nth-child(3) {
        height: 37px;
      }
      &:last-child {
        height: 37px;
        border-bottom: none;
      }
    }
  }

  .list-main {
    margin-bottom: 40px;
    position: relative;
    background-color: @body-background;
    .list-radio {
      position: absolute;
      right: 30px;
      top: 0px;
    }
    .pagena {
      text-align: center;
    }
  }
}
.list-right {
  width: 258px;
  float: right;
  margin-left: 10px;
  background-color: @body-background;
  .list-box {
    margin-bottom: 15px;
    border: 1px solid #eaeaea;
    .list-box-title {
      height: 37px;
      line-height: 37px;
      background-color: #e6e6e6;
      border-bottom: 1px solid #eaeaea;
      font-size: 14px;
      padding: 0 15px;
    }
    .list-box-con {
      padding: 0px 15px 15px;
    }
  }
}
</style>

<style lang="less">
/* Tab  */
.list-main {
  .ivu-tabs-nav-scroll {
    background: #f8f8f9;
  }
  .ivu-tabs-nav-scroll {
    .ivu-tabs-tab {
      background: #f2f2f2;
    }
  }
  .ivu-tabs.ivu-tabs-card {
    & > .ivu-tabs-bar {
      .ivu-tabs-tab {
        height: 32px;
        border-radius: 0;
        border: none;
        &:nth-child(5) {
          margin-left: 510px;
        }
      }
    }
  }
  .ivu-tabs.ivu-tabs-card {
    & > .ivu-tabs-bar {
      .ivu-tabs-tab-active {
        color: #ff3c1b;
      }
    }
  }
  .ivu-tabs {
    border: 1px solid #ececec;
    padding-bottom: 10px;
  }

  .ivu-tabs-nav {
    .ivu-tabs-tab {
      .ivu-icon {
        font-size: 18px;
        margin-right: 0;
      }
    }
  }
  /* .ivu-tabs-tabpane {
    li {
      &:last-child {
        border-bottom: 0;
      }
    }
  } */
}
</style>


