<template>
  <div class="orderconfirm">
    <!-- header -->
    <headertop></headertop>
    <div class="container">
      <!-- step -->
      <!-- <div class="steps">
        <Steps :current="1">
          <Step title=""></Step>
          <Step title=""></Step>
          <Step title=""></Step>
          <Step title=""></Step>
        </Steps>
      </div> -->
      <div class="choose-seat-container">
        <div class="choose-seat-head">
          <div class="head-first">1 </div>
          <div class="head-second">2 </div>
          <div class="head-third">3 </div>
          <div class="head-third">4 </div>
        </div>
      </div>
      <!-- step -->
      <!--  -->
      <!-- <div class="buyerinfo">
                <div class="hd">
                    <h2 class="hd-title"></h2>
                </div>
                <div class="buyer-main">
                    <Card dis-hover>
                        <p slot="title"></p>
                        <p class="username">
                            <Input v-model="username" placeholder=""></Input>
                        </p>
                        <p class="phonenum">
                            <Input v-model="phonenum" placeholder=""></Input>
                        </p>

                        <span class="tips">
                            <em>
                                <Icon type="information"></Icon>
                            </em>
                            
                        </span>
                    </Card>
                </div>
            </div> -->
      <!--  -->

      <!--  -->
      <div class="userid">
        <div class="hd">
          <h2 class="hd-title"></h2>
        </div>
        <div class="user-main">
          
          <Button type="primary" @click="modalconfirm = true"></Button>
          <!--  -->
          <div class="selectbuyer" v-show="modalconfirm">
            <ticketbuyer :params="buyer" :num="ticketnum" @listenbuyer="getbuyerinfo"></ticketbuyer>
          </div>
          <!--  -->

        </div>
      </div>
      <!--  -->
      <!--  -->
      <div class="pay">
        <div class="hd">
          <h2 class="hd-title"></h2>
        </div>
        <div class="pay-main">
          <RadioGroup v-model="pay">
            <Radio label="payinline">
              <span></span>
            </Radio>
          </RadioGroup>
        </div>
      </div>
      <!--  -->
      <!--  -->
      <div class="orderlist">
        <div class="hd">
          <h2 class="hd-title"></h2>
        </div>
        <div class="orderlist-main">
          <Table border :columns="columns1" :data="data1"></Table>
        </div>
      </div>
      <!--  -->
      <!--  -->
      <div class="invoice">
        <div class="hd">
          <h2 class="hd-title"></h2>
        </div>
        <div class="invoice-main">
          <Tabs name="name1" :animated="false" @on-click="invoice">
            <TabPane name="name1" label="">
              <div class="invoice-wrapper clearfix">
                <span class="fl"></span>
                <p class="invoiceHead fl">
                  <input id="inhead" v-model="invoiceHead" placeholder=""></input>
                </p>
              </div>
              <div class="invoice-wrapper clearfix">
                <span class="fl"></span>
                <p class="invoiceId fl">
                  <input id="inid" v-model="invoiceId" placeholder=""></input>
                </p>
              </div>
              <Button type="primary" @click="compsubmit"></Button>
            </TabPane>
            <TabPane name="name2" label="">
              <Button type="primary" @click="percubmit"></Button>
            </TabPane>
          </Tabs>
        </div>
      </div>
      <!--  -->
      <!--  -->
      <div class="discount">
        <div class="discount-bd">
          <p class="row">
            <label class="u-checkbox">
              <input type="checkbox" id="insurance" @click="safe">
              <span class="txt">20.00 </span>
            </label>
          </p>
          <p class="row">
            
            <a href="javascript:;"></a> 
            <a href="javascript:;"></a>
          </p>
          <p class="row"></p>
          <p class="row">
            <span class="cell">{{this.totalpirce}}</span>
          </p>
          <p v-show="insurancefalg" class="row">
            <span class="cell">
              <i>+</i> 20.00</span>
          </p>
        </div>
        <!--  -->
        <div class="ft">
          <p class="row txt">
            
            <span> {{this.paymount}}</span>
          </p>
          <p class="row">
            <label class="u-checkbox">
              <input type="checkbox" id="agreement" @click="agree">
              <span class="txt">
                <Tooltip placement="top">
                  <a href="javascript;;"></a>
                  <div slot="content">
                    <p>/</p>
                    <p>/</p>
                    <p>/</p>
                  </div>
                </Tooltip>
                <Tooltip placement="top">
                  <a href="javascript;;"></a>
                  <div slot="content">
                    <p>1 /</p>
                    <p>2 </p>
                    <p>3 </p>
                  </div>
                </Tooltip>
                <Tooltip placement="top">
                  <a href="javascript;;"></a>
                  <div slot="content">
                    <p>1</p>
                    <p>2/</p>
                  </div>
                </Tooltip>
              </span>
            </label>
          </p>
          <p class="row"></p>
          <Button type="primary" :disabled='disabled' @click="submitorder"></Button>
        </div>
        <!--  -->
      </div>
      <!--  -->
    </div>

    <!-- footer -->
    <footersimple></footersimple>
  </div>
</template>

<script>
/* header */
import headertop from "@/components/headertop";

import footersimple from "@/components/footerSimple";
// 
import ticketbuyer from "@/components/orderlist/ticketbuyer";

export default {
  data() {
    return {
      // id
      userId: "02220",
      // 
      schedulerId: 2,
      // Id
      itemId: 100,
      // Id
      cinemaId: 300,
      // (0:WEB 1: 2:)
      sourceType: 0,
      // 0 1
      isNeedInvoice: 0,
      // 0 1
      invoiceType: 0,
      // 
      invoiceHead: "",
      invoiceId: "",
      // (0 1:)
      isNeedInsurance: 0,
      // (x1_y1,x2_y2,...)
      seatPositions: "",
      // 
      pay: "payinline",
      // 
      columns1: [
        {
          title: "",
          key: "itemName"
        },
        {
          title: "",
          key: "seatPositions"
        },
        {
          title: "",
          key: "price"
        },
        {
          title: "",
          key: "num"
        },
        {
          title: "",
          key: "totalAmount"
        }
      ],
      data1: [
        {
          itemName: "John Brown",
          seatPositions: 18,
          price: "1000",
          num: "1",
          totalAmount: "1000"
        }
      ],
      // 
      disabled: true,
      // total
      totalpirce: 0,
      // 
      insurancefalg: false,
      // 
      paymount: 0,
      // 
      orderNo: "",
      // 
      modalconfirm: false,
      // 
      columnbuyer: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          title: "",
          key: "name"
        },
        {
          title: "",
          key: "cardType"
        },
        {
          title: "",
          key: "idCard"
        }
      ],
      buyer: [],
      // 
      ticketnum: 2,
      // ID
      linkIds: "",
      // 
      seatinfos: {}
    };
  },
  components: {
    headertop,
    footersimple,
    ticketbuyer
  },
  methods: {
    total() {
      var sum = 0;
      this.data1.forEach(item => {
        // 
        sum += parseInt(item.totalAmount);
      });
      this.totalpirce = sum;
      this.paymount = this.totalpirce;
    },
    // 
    safe() {
      var insurance = document.getElementById("insurance");
      if (insurance.checked == true) {
        // 
        this.isNeedInsurance = 1;
        this.insurancefalg = !this.insurancefalg;
        this.paymount = this.totalpirce + 20;
      } else {
        //  
        this.isNeedInsurance = 0;
        this.insurancefalg = !this.insurancefalg;
        this.paymount = this.paymount - 20;
      }
    },
    // 
    agree() {
      var agreement = document.getElementById("agreement");
      if (agreement.checked == true) {
        this.disabled = !this.disabled;
      } else {
        this.disabled = true;
      }
    },
    // 
    invoice(e) {
      let type = e;
      if (type == num1) {
        this.invoiceType = 1;
      } else {
        this.invoiceType = 0;
      }
    },
    // 
    compsubmit() {
      var companyname = document.getElementById("inhead").value;
      var companyid = document.getElementById("inid").value;
      this.invoiceHead = companyname;
      this.invoiceId = companyid;
      // 
      this.isNeedInvoice = 1;
    },
    // 
    percubmit() {
      // 
      this.invoiceHead = "";
      this.invoiceId = "";
      // 
      this.isNeedInvoice = 1;
    },
    // ,
    getbuyerinfo(data) {
      // console.log('data=='+data);
      this.linkIds = data;
    },
    // 
    submitorder() {
      // 
      // 
      this.$http
        .postRequest({
          type: "submitOrder",
          data: {
            // id
            userId: this.userId,
            // 
            schedulerId: this.schedulerId,
            // Id
            itemId: this.itemId,
            // Id
            cinemaId: this.cinemaId,
            // (0:WEB 1: 2:)
            sourceType: this.sourceType,
            // 0 1
            isNeedInvoice: this.isNeedInvoice,
            // 0 1
            invoiceType: this.invoiceType,
            // 
            invoiceHead: this.invoiceHead,
            invoiceId: this.invoiceId,
            // (0 1:)
            isNeedInsurance: this.isNeedInsurance,
            // (x1_y1,x2_y2,...)
            seatPositions: this.seatPositions,
            // 
            linkIds: this.linkIds
          }
        })
        .then(res => {
          // 
          // console.log(" = " + JSON.stringify(res.data.orderNo));
          // 
          this.orderNo = res.data.orderNo;

          // orderNoorderNo
          //-
          if (this.orderNo != "") {
            this.$router.push({
              path: "paymethod",
              query: { orderNo: this.orderNo }
            });
          }
        });
    }
  },
  mounted() {
    // 
    // console.log("seatdata==" + JSON.stringify(this.$route.query));
    this.seatinfos = this.$route.query;
    this.schedulerId = this.seatinfos.comData.schedulerId;
    this.cinemaId = this.seatinfos.comData.cinemaId;
    // 
    this.ticketnum = this.seatinfos.seatData.length;
    // 
    let arr = [];
    this.seatinfos.seatData.forEach(item => {
      arr.push({
        itemName: item.itemName,
        seatPositions: item.x + "" + item.y + "",
        price: item.price,
        num: 1,
        totalAmount: item.price
      });
    });
    this.data1 = arr;
    // 
    let str = "";
    this.seatinfos.seatData.forEach(item => {
      if (str == "") {
        str = item.x + "_" + item.y;
      } else {
        str = str + "," + item.x + "_" + item.y;
      }
    });
    // console.log("str ==" + str);
    // 
    this.seatPositions = str;

    // 
    this.total();
  },
  watch: {
    modalconfirm: function() {
      if (this.modalconfirm == true) {
        this.buyer = this.columnbuyer;
      }
    }
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../static/less/custom.less";
@top: 50px;
@red: #ff3c1b;
.orderconfirm {
  .container {
    padding: 0 20px 50px;
    background-color: @body-background;
  }
  .steps {
    padding: 30px 0;
  }

  .hd {
    position: relative;
    border-bottom: 1px solid #d7d7d7;
    &:after {
      content: "";
      position: absolute;
      left: 0;
      top: 100%;
      z-index: 0;
      width: 120px;
      border-bottom: 1px solid @primary-color;
    }
    .hd-title {
      color: @primary-color;
      line-height: 30px;
      font-size: 16px;
      font-weight: 400;
      padding-left: 10px;
    }
  }
  // 
  .buyerinfo {
    .buyer-main {
      margin-top: 15px;
      .username {
        padding-bottom: 10px;
      }
      .phonenum {
        padding-bottom: 20px;
      }
      .tips {
        margin-top: 20px;
        padding: 10px;
        background: #fdf7e4;
        em {
          margin-right: 5px;
          display: inline-block;
          width: 20px;
          height: 20px;
          line-height: 20px;
          text-align: center;
          border-radius: 20px;
          background: #5893d7;
        }
      }
    }
  }
  //   
  .userid {
    margin-top: 20px;
    .user-main {
      padding: 10px 0;
    }
  }
  // 
  .pay {
    margin-top: 20px;
    .pay-main {
      padding: 10px 0;
    }
  }
  //
  .orderlist {
    margin-top: 20px;
    .orderlist-main {
      padding: 10px 0;
    }
  }
  // 
  .invoice {
    margin-top: 20px;
    margin-bottom: 20px;

    p {
      width: 300px;
    }
    .invoice-wrapper {
      line-height: 50px;
      input {
        width: 300px;
        border: 1px solid #c3c3c3;
        line-height: 30px;
        border-radius: 3px;
        text-indent: 4px;
      }
    }
    button {
      margin: 20px 0 0 30px;
    }
  }
  // 
  .discount-bd {
    padding: 20px 0;
    border-top: 1px solid #c3c3c3;
    text-align: right;
    .row {
      line-height: 24px;
      vertical-align: text-top;
      .u-checkbox {
        input {
          margin-right: 10px;
        }
      }
      .cell {
        margin-left: 15px;
        color: @primary-color;
        i {
          color: #333;
        }
      }
    }
  }
  // 
  .ft {
    padding: 20px 0;
    border-top: 1px dashed #c3c3c3;
    text-align: right;
    .row {
      line-height: 24px;
    }
    .txt {
      font-weight: bold;
      span {
        color: @primary-color;
        font-weight: bold;
      }
    }
    button {
      margin: 20px 0 0 10px;
    }
  }
  // 
  .selectbuyer {
    width: 700px;
    padding: 20px;
    border: 1px solid #efefef;
    margin-top: 20px;
  }
  // step
  .choose-seat-container {
    height: 50px;
    position: relative;
    .choose-seat-head {
      width: 1160px;
      position: absolute;
      top: 10px;
      z-index: 1;
      div {
        float: left;
        display: inline;
        width: 25%;
        height: 50px;
        background: #65a9fd;
        color: #fff;
        text-align: center;
        line-height: 50px;
        font-size: 18px;
        background: linear-gradient(
          #565656,
          #484848,
          #3e3e3e,
          #484848,
          #565656
        );
      }
      .head-second {
        background: #ff3c1b;
        position: relative;
        height: 52px;
        top: -2px;
      }
      .head-second:before {
        content: "";
        position: absolute;
        display: block;
        top: 0px;
        width: 60px;
        border: 25px solid @red;
        border-bottom: 26px solid @red;
        border-top: 26px solid @red;
        height: 50px;
        z-index: 1;
      }
      .head-second:after {
        content: "";
        position: absolute;
        display: block;
        top: -1px;
        width: 60px;
        border: 27px solid @red;
        border-left: 30px solid @red;
        height: 54px;
        z-index: 1;
      }
      .head-second:before {
        left: -26px;
        border-left-color: transparent;
      }
      .head-second:after {
        right: -60px;
        border-top-color: transparent;
        border-bottom-color: transparent;
        border-right-color: transparent;
      }
    }
  }
  // step
}
</style>

<style lang="less">
// 
.invoice {
  .ivu-tabs-bar {
    margin-bottom: 0;
  }
  .ivu-tabs-no-animation {
    .ivu-tabs-content {
      padding: 15px;
      height: 200px;
      border: 1px solid #dddee1;
      border-top: none;
    }
  }
}
.discount {
  .ivu-tooltip-inner {
    white-space: normal;
  }
}
</style>





