<template>
    <div class="ticketbuyer">
        <div class="buyer-btn">
            <Button type="error" @click="modalbuyer = true"></Button>

            <Modal v-model="modalbuyer" title="" @on-ok="ok">
                <!--  -->
                <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
                    <FormItem label="" prop="name">
                        <Input v-model="formValidate.name" placeholder=""></Input>
                    </FormItem>
                    <FormItem label="" prop="idCard">
                        <Input v-model="formValidate.idCard" placeholder=""></Input>
                    </FormItem>
                    <FormItem label="" prop="cardType">
                        <Select v-model="formValidate.cardType" placeholder="">
                            <Option value="0"></Option>
                            <Option value="1" disabled></Option>
                        </Select>
                    </FormItem>
                </Form>
                <!--  -->
            </Modal>
        </div>
        <Table border :columns="columns" :data="databuyer" @on-selection-change="selectoption"></Table>
    </div>
</template>

<script>
export default {
  data() {
    return {
      columns: [
        {
          type: "index",
          width: 60,
          align: "center"
        },
        {
          title: "",
          key: "name"
        },
        {
          title: "",
          key: "cardType"
        },
        {
          title: "",
          key: "idCard"
        },
        {
          title: "",
          key: "action",
          width: 150,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.remove(params.index, params.row.linkId);
                      //   console.log("params="+JSON.stringify(params))
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      databuyer: [
        /*{
          name: "John Brown",
          cardType: "",
          idCard: "37088923839435454"
        } */
      ],
      // 
      modalbuyer: false,
      // 
      /* 
      name		
      idCard		
      cardType		(0:,1:) 
      */
      formValidate: {
        // 
        name: "",
        // 
        idCard: "",
        // 
        cardType: ""
      },
      // 
      ruleValidate: {
        name: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        idCard: [
          {
            required: true,
            message: "",
            trigger: "blur"
          },
          {
            type: "string",
            min: 15,
            message: "15",
            trigger: "blur"
          }
        ],
        cardType: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      linkIds: []
    };
  },
  components: {},
  props: ["params", "num"],
  methods: {
    // 
    remove(index, linkId) {
      // 
      this.databuyer.splice(index, 1);
      // linkId
      this.$http
        .postRequest({
          type: "deleteTicketBuyer",
          data: {
            //  ID
            linkId: linkId
          }
        })
        .then(res => {
          //  
          //  console.log(" = " + JSON.stringify(res.data));
          //  (0:,1:)
          //   this.databuyer = res.data;
          //  
          this.getbuyerlist();
        });
    },
    // 
    getbuyerlist() {
      this.$http
        .postRequest({
          type: "ticketBuyerList"
        })
        .then(res => {
          //  
          //  console.log(" = " + JSON.stringify(res.data));
          //  (0:,1:)
          for (var i in res.data) {
            if (res.data[i].cardType == 0) {
              res.data[i].cardType = "";
            } else {
              res.data[i].cardType = "";
            }
          }
          this.databuyer = res.data;
        });
    },
    // 
    addbuyer() {
      //   console.log("this.formValidate ===" + JSON.stringify(this.formValidate));
      this.$http
        .postRequest({
          type: "addTicketBuyer",
          // 
          data: this.formValidate
        })
        .then(res => {
          //  
          console.log("= " + res.data);
        });
    },
    // 
    setNull() {
      this.formValidate.name = "";
      this.formValidate.idCard = "";
      this.formValidate.cardType = "0";
    },
    // 
    validateexist() {
      this.$http
        .postRequest({
          type: "validateBuyerExist",
          data: {
            //  ID
            idCard: this.formValidate.idCard
          }
        })
        .then(res => {
          //  
          if (res.errorCode == 1009) {
            this.$Notice.warning({
              title: ""
            });
            // 
            this.setNull();
          } else {
            //  
            this.addbuyer();

            this.$Notice.warning({
              title: ""
            });
            // 
            this.setNull();
            // 
            this.getbuyerlist();
          }
        });
    },
    // 
    ok() {
      // 
      this.validateexist();
    },
    // 
    selectoption(e) {
      //   console.log(JSON.stringify(e));
      //   console.log(e.length);
      if (e.length == this.num) {
        this.$Notice.success({
          title: ","
        });
        for (var i = 0; i < e.length; i++) {
          this.linkIds.push(e[i].linkId);
        }
        this.$emit("listenbuyer", this.linkIds.toString());
        this.linkIds = [];
      } else if (e.length > this.num) {
        this.$Notice.error({
          title: ""
        });
      }
      // 
      //   console.log("this.linkIds=" + typeof this.linkIds.toString());
      //   console.log("this.linkIds=" + this.linkIds.length);
      //   console.log("this.num=" + this.num);
    }
  },
  mounted() {
    // 
    this.getbuyerlist();
  },
  watch: {
    params: function() {
      //   console.log("params=" + JSON.stringify(this.params));
      //   param
      this.columns = this.params;
    }
  }
};
</script>

<style lang="less" scoped>
.buyer-btn {
  padding: 0 0 20px;
  overflow: hidden;
  button {
    float: right;
  }
}
</style>



