<template>
  <div class="orderlist">
    <!--  -->
    <Vheader/>
    <!-- main -->
    <div class="list-main">
      <div class="container">
        <div class="layout clearfix">
          <Layout>
            <Layout>
              <Sider :style="{background: '#fff',width: '200px',float: 'left',margin: '0 20px 0 0'}">
                <Menu @on-select="handleChange" active-name="" theme="light" width="auto" :open-names="['1']" @on-collapse="handleChange">
                  <MenuItem name="3" :style="{background: '#FF5C41',color:'#fff'}"></MenuItem>
                  <Submenu name="1">
                    <template slot="title">
                      
                    </template>
                    <MenuItem name=""></MenuItem>
                  </Submenu>
                  <Submenu name="2">
                    <template slot="title">
                      
                    </template>
                    <MenuItem name=""></MenuItem>
                    <MenuItem name=""></MenuItem>
                  </Submenu>
                </Menu>
              </Sider>
              <Layout :style="{width:'980px',float: 'left'}">
                <div class="breadcrumb h5">
                  <a href="javascript:;" target="_self"></a>&gt;
                  <span>{{this.menuName}}</span>&gt;
                  <span>{{this.submenuName}}</span>
                </div>
                <!--  -->
                <recentlyorderlist v-show="ordershow"></recentlyorderlist>
                <!--  -->

                <!--  -->
                <personinfo v-show="personinfoshow" :infos="perinfos" :isinfo="isperinfo"></personinfo>
                <!--  -->
                <!--  -->
                <ticketbuyer v-show="tbuyershow"></ticketbuyer>
                <!--  -->
              </Layout>
            </Layout>
          </Layout>
        </div>
      </div>
    </div>
    <!--  -->
    <Vfooter/>
  </div>
</template>

<script>
// 
import header from "@/components/header";
// 
import footer from "@/components/footer";
// 
import recentlyorderlist from "@/components/orderlist/recentlyorderlist";
// 
import personinfo from "@/components/orderlist/personinfo";
// 
import ticketbuyer from "@/components/orderlist/ticketbuyer";

export default {
  data() {
    return {
      // 
      userId: "",
      // 
      menuName: "",
      submenuName: "",
      /* menuName: "",
      submenuName: "", */
      // 
      ordershow: true,
      // 
      personinfoshow: false,
      // 
      tbuyershow: false,
      // 
      perinfos: {},
      isperinfo: ""
    };
  },
  components: {
    Vheader: header,
    Vfooter: footer,
    recentlyorderlist,
    personinfo,
    ticketbuyer
  },
  methods: {
    handleChange(e) {
      //   console.log("==" + e);
      this.submenuName = e;
      if (e == "") {
        this.ordershow = true;
        this.personinfoshow = false;
        this.tbuyershow = false;

        this.menuName = "";
      } else if (e == "") {
        this.ordershow = false;
        this.personinfoshow = true;
        this.tbuyershow = false;

        this.menuName = "";
      } else {
        this.ordershow = false;
        this.personinfoshow = false;
        this.tbuyershow = true;

        this.menuName = "";
      }
    },
    // Id
    getPersonIofo() {
      this.$http
        .postRequest({
          type: "queryPersonInfo",
          data: {
            //  ID
            userId: this.userId
          }
        })
        .then(res => {
          //  
          // console.log(" = " + JSON.stringify(res.data));
          //   formValidate,
          // this.formValidate = res.data;
          this.perinfos = res.data;
          // res.data.birthday = res.data.birthday.getTime();
          this.isperinfo = JSON.stringify(res.data);
          // console.log("perinfos = " + JSON.stringify(this.perinfos));
        });
    }
  },
  mounted() {
    // ID?
    this.userId = "12345";
    // 
    this.getPersonIofo();
  }
};
</script>

<style lang="less" scoped>
// iview
@import "../../../static/less/custom.less";
.orderlist {
  .list-main {
    width: 100%;
    border-top: 2px solid @primary-color;
    padding: 20px 0;
    .breadcrumb {
      border: 1px solid #efefef;
      height: 38px;
      overflow: hidden;
      padding: 0 15px;
      line-height: 38px;
      color: #333;
      margin-bottom: 15px;
      background: linear-gradient(top, #efefef, #fff);
    }
  }
}
</style>
<style lang="less">
.orderlist {
  .ivu-menu-vertical {
    .ivu-menu-item:hover,
    .ivu-menu-submenu-title:hover {
      background: #e6e6e6;
    }
  }
  .ivu-layout {
    background: #fff;
  }
  .ivu-menu-light {
    border: 1px solid #efefef;
  }
  .ivu-layout-sider {
    z-index: 1;
  }
}
</style>




